import Users from './index';
import {connect} from "react-redux";
import {useEffect} from "react";
import {compose} from "redux";
import {
    setCurrentPage,
    setTotal,
    toggleFollow,
    fetchUsers
} from '../../redux/users/reducer';
import {
    getFollowingInProgress,
    getFriends,
    getLoading,
    getPage,
    getTotalUsersCount,
    getUsers
} from "../../redux/users/selectors";
// import withAuthRedirectComponent from "../hoc/withAuthRedirect";

const UsersAPIContainer = props => {
    let {
        page,
        total,
        fetchUsers,
        setCurrentPage,
        location: {search},
        ...other
    } = props;

    useEffect(()=> {
        const searchPage = Number(new URLSearchParams(search).get('page')) || page;
        searchPage !== page
            ? setCurrentPage(searchPage)
            : fetchUsers(page);

    }, [page, setCurrentPage, fetchUsers, search]);

    return Users({page, total, ...other});
};

export default compose(
    connect(
        state => ({
            loading: getLoading(state),
            users: getUsers(state),
            friends: getFriends(state),
            page: getPage(state),
            total: getTotalUsersCount(state),
            followingInProgress: getFollowingInProgress(state),
            myId: state.authData.id,
            isAuth: state.authData.isAuth
        }),
        {
            setCurrentPage,
            toggleFollow,
            setTotal,
            fetchUsers
        }
    ),
    // withAuthRedirectComponent,
)(UsersAPIContainer)

