import React from 'react';
import s from './Users.module.scss';
import ListItem from './ListItem'
import Pagination from '../common/Pagination'
import Preloader from '../common/Preloader'

export default props => {
    if(!props.users.length) return <Preloader loading={true}/>;

    const {
        loading,
        users,
        page,
        total,
        ...other
    } = props;

    const setPage = page => {
        props.history.push(`/users?page=${page}`);
    };

    return (
        <div className={s.Users} key={'Users'}>
            <div className={s.title}>Users</div>

            <Preloader loading={loading}/>

            <ul className={s.list + (loading ? ' ' + s.loading: '')}>
                {users.map(user => ListItem({user, ...other}))}
            </ul>

            <Pagination page={page} total={total} onClickHandler={setPage}/>
        </div>
    );
}

